<template>
    <v-container>
        <section class="mb-5">
            <v-layout justify-center>
                <h2 class="headline mb-5">Profile details</h2>
            </v-layout>
            <v-layout>
                <v-text-field
                  :value="user.name"
                  :disabled="true"
                  label="Username"
                >
                </v-text-field>
            </v-layout>
            <v-layout>
                <v-text-field
                  v-model="fullName"
                  :placeholder="user.fullName"
                  label="Full name"
                >
                </v-text-field>
            </v-layout>
            <v-layout>
                <v-text-field
                  v-model="company"
                  :placeholder="user.company"
                  label="Company"
                ></v-text-field>
            </v-layout>
            <v-layout>
                <v-text-field
                  v-model="phone"
                  :placeholder="user.phone"
                  label="Phone"
                ></v-text-field>
            </v-layout>
            <v-layout>
                <v-text-field
                  v-model="skypeName"
                  :placeholder="user.skypeName"
                  label="Skype name"
                ></v-text-field>
            </v-layout>
            <v-layout>
                <v-text-field
                  v-model="email"
                  :placeholder="user.email"
                  label="E-mail"
                ></v-text-field>
            </v-layout>
            <v-layout>
                <v-btn @click="save">save</v-btn>
            </v-layout>
        </section>
        <v-divider></v-divider>
            <v-layout
                row
                align-center
                justify-center
                wrap
                class="mt-5">
                <v-flex xs12 md12 class="mb-2">
                    <h2 class="headline">Favorite countries</h2>
                </v-flex>
                <v-flex xs12 md6>
                    <v-autocomplete
                        v-model="countries"
                        :disabled="isUpdating"
                        :items="getCountries"
                        box
                        chips
                        color="blue-grey lighten-2"
                        label="Select"
                        item-text="name"
                        item-value="name"
                        multiple
                        >
                        <template
                            slot="selection"
                            slot-scope="data"
                        >
                            <v-chip
                            :selected="data.selected"
                            close
                            class="chip--select-multi"
                            @input="remove(data.item)"
                            >
                            {{ data.item }}
                            </v-chip>
                        </template>
                        <template
                            slot="item"
                            slot-scope="data"
                        >
                            <template v-if="typeof data.item !== 'object'">
                            <v-list-tile-content v-text="data.item"></v-list-tile-content>
                            </template>
                            <template v-else>
                            <v-list-tile-avatar>
                                <img :src="data.item.avatar">
                            </v-list-tile-avatar>
                            <v-list-tile-content>
                                <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                                <v-list-tile-sub-title v-html="data.item.group"></v-list-tile-sub-title>
                            </v-list-tile-content>
                            </template>
                        </template>
                        </v-autocomplete>
                </v-flex>
                <v-flex xs12 class="text-xs-center">
                    <v-btn @click="saveCountries">save</v-btn>
                </v-flex>
            </v-layout>
    </v-container>    
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    data () {
        return {
            user: '',
            fullName: '',
            company: '',
            phone: '',
            skypeName: '',
            email: '',
            loading: false,
            countries: [],
            items: [],
            search: null,
            select: null,
            isUpdating: false
        }
    },
    mounted () {
        this.user = JSON.parse(localStorage.getItem('user'))
        this.countries = this.getFavoriteCountries
        this.items = this.getCountries
    },
   watch: {
      isUpdating (val) {
        if (val) {
          setTimeout(() => (this.isUpdating = false), 3000)
        }
      }
    },
    computed: {
        ...mapGetters([
            'getCountries',
            'getFavoriteCountries'
        ])
    },
    methods: {
        ...mapActions([
            'updateProfile'
        ]),

        remove (item) {
            const index = this.countries.indexOf(item)
            if (index >= 0) this.countries.splice(index, 1)
        },

        saveCountries () {
            var newCountries = {
                favoriteCountries: this.countries
            }
            this.updateProfile(newCountries)
        },

        save () {
            var newUser = {
                fullName: this.fullName !== '' ? this.fullName : this.user.fullName,
                company: this.company !== '' ? this.company : this.user.company,
                phone: this.phone !== '' ? this.phone : this.user.phone,
                skypeName: this.skypeName !== '' ? this.skypeName : this.user.skypeName,
                email: this.email !== '' ? this.email : this.user.email
            }
            // TO DO: Updating profile, currently not working..
            this.updateProfile(newUser)
            this.user = newUser
        }
    }
}
</script>

<style scoped>

</style>


