import api from '@/services/api/'

const state = {
    user: JSON.parse(localStorage.getItem('user')),
    favoriteCountries: [],
    users: []
}

const getters = {
    getFavoriteCountries: state => {
        return state.favoriteCountries
    },
    getUsers: state => {
        return state.users
    }
}

const mutations = {
    updateFavoriteCountries: (state, payload) => {
        state.favoriteCountries = payload
    },
    getUsersToStore: (state, payload) => {
        state.users = payload
    }
}

const actions = {
    updateProfile({commit}, creds) {
        return api.updateProfile(creds).then(result => {
            if (creds.favoriteCountries) {
                commit('updateFavoriteCountries', creds.favoriteCountries)
            }
        })
    },
    getUsersToStore({commit}) {
        return api.getUsers().then(result => {
            console.log(result.data)
            commit('getUsersToStore', result.data)
        })
    },
    getFavoriteCountriesToStore({commit}) {
        if (localStorage.getItem('user')) {
            commit('updateFavoriteCountries', JSON.parse(localStorage.getItem('user')).favoriteCountries) 
        }
    },
    sendEmailToUser({commit}) {
        return api.sendEmail().then(result => {
            console.log(result)
        })
    }
}

export default {
state,
getters,
mutations,
actions
}
  