<template>
    <v-container>
        <section>
            <v-layout
                row
                align-center
                wrap
                class="mb-5">
                <v-flex xs12 md4 class="mb-2">
                    <h2 class="headline">Change password</h2>
                </v-flex>
                <v-flex xs12 md4>
                    <v-text-field
                      label="Old password"
                      type="password"
                      v-model="oldPassword"
                      >
                    </v-text-field>
                    <v-text-field 
                      label="New password"
                      type="password"
                      v-model="newPassword"
                      >
                    </v-text-field>
                    <v-text-field
                      label="Confirm new password"
                      type="password"
                      v-model="confirmNewPassword"
                      >
                    </v-text-field>
                    <v-btn @click="changePassword">change</v-btn>
                </v-flex>
            </v-layout>
        </section>
    </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    data () {
        return {
            oldPassword: '',
            newPassword: '',
            confirmNewPassword: ''
        }
    },
    methods: {
        ...mapActions([
            'updateProfile'
        ]),
        changePassword () {
            var user = JSON.parse(localStorage.getItem('user'))
            if (user.password === this.oldPassword) {
                if (this.newPassword === this.confirmNewPassword) {
                    var newUser = {
                        password: this.newPassword
                    }
                    this.updateProfile(newUser)
                    alert('Password changed')
                } else {
                    alert('Not confirmed well')
                }
            } else {
                alert('Wrong old password')
            }
        }
    }
}
</script>

